import { FunctionalComponent, h } from "preact"
import { useState } from "preact/hooks"
import * as style from "./style.scss"

const Portfolio: FunctionalComponent = () => {
    const [ project, setProject ] = useState(null)
    const projects = [
        {
            image: require('../../assets/images/emerhub.png').default,
            name: 'Emerhub App',
            description: `App built to help customers create outsourcing companies in Southeast asia`,
            website: 'https://emerhub.com'
        },
        {
            image: require('../../assets/images/ticketon.jpg').default,
            name: 'Ticket on',
            description: `POS system for direct sales,
                with the power to work offline and control/send messages to multiple terminals`,
                website: 'http://www.datahex.com.br'
        },
        {
            image: require('../../assets/images/TOTVS.jpg').default,
            name: 'ERP Integration',
            description: `Made erp integration with an Ecommerce, Powerbi and Gooddata.
                This allowed our client check and manage his business anywhere.`,
            website: 'https://totvs.com'
        }
    ]
    return (
        <div class={style.portfolio}>
            {
                project &&
                <div>
                    <svg onClick={() => setProject(null)} class={style.projectClose} fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg>
                    <div class={style.project}>
                        <h2>{ project.name }</h2>
                        <p>{ project.description }</p>
                        <p>
                            Find more info at: <a href={project.website} target="_blank">{ project.website }</a>
                        </p>
                    </div>
                </div>
            }
            {
                !project &&
                projects.map(project =>
                    <div class={style.projectThumbnail} onClick={() => setProject(project)}>
                        <img class={style.projectThumbnailImage} src={project.image} />
                        <span class={style.projectThumbnailName}>{ project.name }</span>
                    </div>
                )
            }
        </div>
    )
}

export default Portfolio
